

#manualBtn{
  border: solid;
  border-width: 1px;
  border-color: rgb(238, 199, 172);
  padding: 25px;
  background-color: white;
  text-decoration: none;
}

#cameraBtn{
  border: solid;
  border-width: 1px;
  border-color: rgb(238, 199, 172);
  padding: 25px;
  background-color: white;
  text-decoration: none;
}

#container {
  width: 640px;
  margin: 20px auto;
  padding: 10px;
}

#scanner-container.viewport {
  width: 640px;
  height: 480px;
}


#scanner-container.viewport canvas, video {
  float: left;
  width: 640px;
  height: 480px;
}

#scanner-container.viewport canvas.drawingBuffer, video.drawingBuffer {
  margin-left: -640px;
}

.controls fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.controls .input-group {
  float: left;
}

.controls .input-group input, .controls .input-group button {
  display: block;
}

.controls .reader-config-group {
  float: right;
}

.controls .reader-config-group label {
  display: block;
}

.controls .reader-config-group label span {
  width: 9rem;
  display: inline-block;
  text-align: right;
}

.controls:after {
  content: '';
  display: block;
  clear: both;
}


#result_strip {
  margin: 10px 0;
  border-top: 1px solid #EEE;
  border-bottom: 1px solid #EEE;
  padding: 10px 0;
}

#result_strip > ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
  width: auto;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
}

#result_strip > ul > li {
  display: inline-block;
  vertical-align: middle;
  width: 160px;
}

#result_strip > ul > li .thumbnail {
  padding: 5px;
  margin: 4px;
  border: 1px dashed #CCC;
}

#result_strip > ul > li .thumbnail img {
  max-width: 140px;
}

#result_strip > ul > li .thumbnail .caption {
  white-space: normal;
}

#result_strip > ul > li .thumbnail .caption h4 {
  text-align: center;
  word-wrap: break-word;
  height: 40px;
  margin: 0px;
}

#result_strip > ul:after {
  content: "";
  display: table;
  clear: both;
}


.scanner-overlay {
  display: none;
  width: 640px;
  height: 510px;
  position: absolute;
  padding: 20px;
  top: 50%;
  margin-top: -275px;
  left: 50%;
  margin-left: -340px;
  background-color: #FFF;
  -webkit-box-shadow: #333333 0px 4px 10px;
  box-shadow: #333333 0px 4px 10px;
}

.scanner-overlay > .header {
  position: relative;
  margin-bottom: 14px;
}

.scanner-overlay > .header h4, .scanner-overlay > .header .close {
  line-height: 16px;
}

.scanner-overlay > .header h4 {
  margin: 0px;
  padding: 0px;
}

.scanner-overlay > .header .close {
  position: absolute;
  right: 0px;
  top: 0px;
  height: 16px;
  width: 16px;
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  cursor: pointer;
}


i.icon-24-scan {
  width: 24px;
  height: 24px;
  background-image: url("data:image/png;base64,");
  display: inline-block;
  background-repeat: no-repeat;
  line-height: 24px;
  margin-top: 1px;
  vertical-align: text-top;
}

@media (max-width: 603px) {

  #cameraBtn{
      padding: 10px;
  }

  #manualBtn{
    padding: 10px;
  }

  .buttonDiv{
    padding: 10px;
    margin-top: 10px;
  }
  #container {
    width: 300px;
    margin: 10px auto;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  #container form.voucher-form input.voucher-code {
    width: 180px;
  }
}
@media (max-width: 603px) {

  .reader-config-group {
    width: 100%;
  }

  .reader-config-group label > span {
      width: 50%;
  }

  .reader-config-group label > select, .reader-config-group label > input {
      max-width: calc(50% - 2px);
  }

  #scanner-container.viewport {
    width: 300px;
    height: 300px;
    overflow: hidden;
  }


  #scanner-container.viewport canvas, video {
    margin-top: -50px;
    width: 300px;
    height: 400px;
  }

  #scanner-container.viewport canvas.drawingBuffer, video.drawingBuffer {
    margin-left: -300px;
  }


  #result_strip {
    margin-top: 5px;
    padding-top: 5px;
  }

  #result_strip ul.thumbnails > li {
    width: 150px;
  }

  #result_strip ul.thumbnails > li .thumbnail .imgWrapper {
    width: 130px;
    height: 130px;
    overflow: hidden;
  }

  #result_strip ul.thumbnails > li .thumbnail .imgWrapper img {
    margin-top: -25px;
    width: 130px;
    height: 180px;
  }
}
@media (max-width: 603px) {

  .overlay.scanner {
    width: 640px;
    height: 510px;
    padding: 20px;
    margin-top: -275px;
    margin-left: -340px;
    background-color: #FFF;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  .overlay.scanner > .header {
    margin-bottom: 14px;
  }

  .overlay.scanner > .header h4, .overlay.scanner > .header .close {
    line-height: 16px;
  }

  .overlay.scanner > .header .close {
    height: 16px;
    width: 16px;
  }
}

.list{
  padding-top: 50px;
  padding-bottom: 50px;
}

#headerOne {
  background-color: rgb(241, 236, 222);
  color: black;
  border: solid;
  border-width: 1px;
  border-color: rgb(238, 199, 172);
  text-align: center;
  z-index: 0;
}

.modal-header, .modal-footer{
  background-color: rgb(241, 236, 222);
}

.modal-body{
  background-color: rgb(194, 224, 231);
}

#scanner_input{
  width: 100%;
}

.card{
  border: solid;
  border-width: 1px;
  border-color: rgb(238, 199, 172);
  margin-bottom: 10px;
  background-color: white;
}

.card-block, .card-footer{
  padding: 25px;
}

.card-title{
  font-weight: bold;
}

.card-footer{
  border-top: solid;
  border-color: rgb(238, 199, 172);
  border-width: 1px;
}

.card-img-top{
  xwidth: 100%;
 
}

.form-control, .form-group{
  width: 100%;
}

form {
  width: 100%;
}


